<template>
  <div class="main-container">
    <div class="tool-header">
      <h1>⏰ 时间戳转换工具</h1>
      <p>Unix时间戳与日期时间的相互转换，支持多种格式</p>
    </div>

    <el-row :gutter="20" class="main-row">
      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="converter-card">
          <div slot="header">
            <span>时间戳转换</span>
          </div>
          
          <el-form label-width="120px">
            <el-form-item label="当前时间戳">
              <el-input :value="currentTimestamp" readonly>
                <el-button slot="append" @click="copyToClipboard(currentTimestamp)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </el-input>
              <div class="help-text">当前Unix时间戳（秒）</div>
            </el-form-item>

            <el-form-item label="输入时间戳">
              <el-input 
                v-model="inputTimestamp" 
                placeholder="请输入Unix时间戳"
                @input="convertFromTimestamp"
              >
                <el-select v-model="timestampUnit" slot="prepend" @change="convertFromTimestamp">
                  <el-option label="秒" value="seconds"></el-option>
                  <el-option label="毫秒" value="milliseconds"></el-option>
                </el-select>
              </el-input>
            </el-form-item>

            <el-form-item label="转换结果">
              <el-input :value="timestampResult" readonly>
                <el-button slot="append" @click="copyToClipboard(timestampResult)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </el-input>
            </el-form-item>

            <el-divider>日期转时间戳</el-divider>

            <el-form-item label="选择日期时间">
              <el-date-picker
                v-model="selectedDate"
                type="datetime"
                placeholder="选择日期时间"
                format="yyyy-MM-dd HH:mm:ss"
                value-format="yyyy-MM-dd HH:mm:ss"
                @change="convertToTimestamp"
                style="width: 100%"
              ></el-date-picker>
            </el-form-item>

            <el-form-item label="时间戳结果">
              <el-input :value="dateResult" readonly>
                <el-button slot="append" @click="copyToClipboard(dateResult)">
                  <i class="el-icon-copy-document"></i>
                </el-button>
              </el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="setCurrentTime">
                <i class="el-icon-time"></i> 使用当前时间
              </el-button>
              <el-button @click="clearAll">
                <i class="el-icon-delete"></i> 清空
              </el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
        <el-card class="format-card">
          <div slot="header">
            <span>多格式显示</span>
          </div>
          
          <el-descriptions :column="1" border v-if="formatResults.timestamp">
            <el-descriptions-item label="Unix时间戳(秒)">
              <span class="format-value">{{ formatResults.timestamp }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.timestamp)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="Unix时间戳(毫秒)">
              <span class="format-value">{{ formatResults.timestampMs }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.timestampMs)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="ISO 8601">
              <span class="format-value">{{ formatResults.iso }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.iso)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="RFC 2822">
              <span class="format-value">{{ formatResults.rfc }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.rfc)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="本地时间">
              <span class="format-value">{{ formatResults.local }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.local)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="UTC时间">
              <span class="format-value">{{ formatResults.utc }}</span>
              <el-button size="mini" @click="copyToClipboard(formatResults.utc)">复制</el-button>
            </el-descriptions-item>
            <el-descriptions-item label="相对时间">
              <span class="format-value">{{ formatResults.relative }}</span>
            </el-descriptions-item>
          </el-descriptions>

          <div v-else class="no-data">
            <i class="el-icon-time"></i>
            <p>请输入时间戳或选择日期时间</p>
          </div>
        </el-card>

        <el-card class="quick-card" style="margin-top: 20px;">
          <div slot="header">
            <span>快速时间戳</span>
          </div>
          
          <div class="quick-buttons">
            <el-button 
              v-for="quick in quickTimestamps" 
              :key="quick.label"
              size="small"
              @click="useQuickTimestamp(quick.value)"
            >
              {{ quick.label }}
            </el-button>
          </div>
        </el-card>

        <el-card class="info-card" style="margin-top: 20px;">
          <div slot="header">
            <span>时间戳说明</span>
          </div>
          
          <ul>
            <li><strong>Unix时间戳：</strong>从1970年1月1日00:00:00 UTC开始的秒数</li>
            <li><strong>毫秒时间戳：</strong>Unix时间戳乘以1000</li>
            <li><strong>ISO 8601：</strong>国际标准日期时间格式</li>
            <li><strong>RFC 2822：</strong>邮件和HTTP协议使用的日期格式</li>
          </ul>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'TimestampView',
  metaInfo: {
    title: '时间戳转换工具 - Unix时间戳转换',
    meta: [
      { name: 'description', content: '在线时间戳转换工具，支持Unix时间戳与日期时间的相互转换，提供多种时间格式显示。' },
      { name: 'keywords', content: '时间戳转换,Unix时间戳,日期转换,时间格式,在线工具' }
    ]
  },
  data() {
    return {
      currentTimestamp: '',
      inputTimestamp: '',
      timestampUnit: 'seconds',
      timestampResult: '',
      selectedDate: '',
      dateResult: '',
      formatResults: {},
      quickTimestamps: [
        { label: '1小时前', value: -3600 },
        { label: '1天前', value: -86400 },
        { label: '1周前', value: -604800 },
        { label: '1月前', value: -2592000 },
        { label: '1年前', value: -31536000 }
      ]
    }
  },
  mounted() {
    this.updateCurrentTimestamp()
    // 每秒更新当前时间戳
    setInterval(this.updateCurrentTimestamp, 1000)
  },
  methods: {
    updateCurrentTimestamp() {
      this.currentTimestamp = Math.floor(Date.now() / 1000).toString()
    },

    convertFromTimestamp() {
      if (!this.inputTimestamp) {
        this.timestampResult = ''
        this.formatResults = {}
        return
      }

      try {
        let timestamp = parseInt(this.inputTimestamp)
        if (isNaN(timestamp)) {
          this.timestampResult = '无效的时间戳'
          return
        }

        // 根据单位转换
        if (this.timestampUnit === 'milliseconds') {
          timestamp = Math.floor(timestamp / 1000)
        }

        const date = new Date(timestamp * 1000)
        
        if (isNaN(date.getTime())) {
          this.timestampResult = '无效的时间戳'
          return
        }

        this.timestampResult = date.toLocaleString('zh-CN', {
          year: 'numeric',
          month: '2-digit',
          day: '2-digit',
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit'
        })

        this.updateFormatResults(date)
      } catch (error) {
        this.timestampResult = '转换失败'
      }
    },

    convertToTimestamp() {
      if (!this.selectedDate) {
        this.dateResult = ''
        this.formatResults = {}
        return
      }

      try {
        const date = new Date(this.selectedDate)
        const timestamp = Math.floor(date.getTime() / 1000)
        this.dateResult = timestamp.toString()
        this.updateFormatResults(date)
      } catch (error) {
        this.dateResult = '转换失败'
      }
    },

    updateFormatResults(date) {
      const timestamp = Math.floor(date.getTime() / 1000)
      
      this.formatResults = {
        timestamp: timestamp.toString(),
        timestampMs: date.getTime().toString(),
        iso: date.toISOString(),
        rfc: date.toUTCString(),
        local: date.toLocaleString('zh-CN'),
        utc: date.toUTCString(),
        relative: this.getRelativeTime(date)
      }
    },

    getRelativeTime(date) {
      const now = new Date()
      const diff = now.getTime() - date.getTime()
      const seconds = Math.floor(diff / 1000)
      const minutes = Math.floor(seconds / 60)
      const hours = Math.floor(minutes / 60)
      const days = Math.floor(hours / 24)

      if (seconds < 60) return `${seconds}秒前`
      if (minutes < 60) return `${minutes}分钟前`
      if (hours < 24) return `${hours}小时前`
      if (days < 30) return `${days}天前`
      if (days < 365) return `${Math.floor(days / 30)}个月前`
      return `${Math.floor(days / 365)}年前`
    },

    useQuickTimestamp(offset) {
      const timestamp = Math.floor(Date.now() / 1000) + offset
      this.inputTimestamp = timestamp.toString()
      this.convertFromTimestamp()
    },

    setCurrentTime() {
      this.selectedDate = new Date().toISOString().slice(0, 19).replace('T', ' ')
      this.convertToTimestamp()
    },

    clearAll() {
      this.inputTimestamp = ''
      this.timestampResult = ''
      this.selectedDate = ''
      this.dateResult = ''
      this.formatResults = {}
    },

    async copyToClipboard(text) {
      if (!text) {
        this.$message.warning('没有可复制的内容')
        return
      }

      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('已复制到剪贴板')
        } else {
          this.$message.error('复制失败')
        }
      } catch (error) {
        this.$message.error('复制失败: ' + error.message)
      }
    }
  }
}
</script>

